.form {
    width: 1180px;
    height: 270px;
    border: 1px solid black;
    border-radius: 20px;
    background: white;
    margin-left: 370px;
    margin-top: 129px;
    position: relative;
    bottom: -40px;
    z-index: 2;
  }
  .form__union {
    position: absolute;
    top: 40px;
    left: 230px;
    z-index: 1;
  }
  .form__owl {
    position: absolute;
    top: 24px;
    left: -50px;
  }
  .form__union_text {
    width: 353px;
    height: 152px;
    color: black;
    font-family: Montserrat;
    font-size: 16px;
    font-weight: 500;
    line-height: 26px;
    margin-top: 62.4px;
    margin-left: 281.4px;
    position: relative;
    z-index: 2;
  }
  .form__union_text span {
    font-size: 24px;
    font-weight: 600;
  }
  .form_area {
    width: 440px;
    display: grid;
    grid-template: 50px 50px 55px/210px 210px;
    gap: 20px;
    position: absolute;
    top: 40px;
    right: 40px;
  }
  .form__name {
    grid-area: 1/1/2/3;
    border: 1px solid rgba(0, 9, 60, 0.1);
    border-radius: 6px;
    background-color: rgba(0, 9, 60, 0.1);
  }
  .form__name::placeholder {
    padding-top: 15px;
    padding-left: 19px;
    color: rgb(79, 79, 79);
    font-family: Montserrat;
    font-size: 14px;
    font-weight: 400;
  }
  .form__number {
    grid-area: 2/1/3/2;
    border: 1px solid rgba(0, 9, 60, 0.1);
    border-radius: 6px;
    background-color: rgba(0, 9, 60, 0.1);
  }
  .form__number::placeholder {
    padding-top: 15px;
    padding-left: 19px;
    color: rgb(79, 79, 79);
    font-family: Montserrat;
    font-size: 14px;
    font-weight: 400;
  }
  .form__email {
    grid-area: 2/2/3/3;
    border: 1px solid rgba(0, 9, 60, 0.1);
    border-radius: 6px;
    background-color: rgba(0, 9, 60, 0.1);
  }
  .form__email::placeholder {
    padding-top: 15px;
    padding-left: 19px;
    color: rgb(79, 79, 79);
    font-family: Montserrat;
    font-size: 14px;
    font-weight: 400;
  }
  .form__text {
    grid-area: 3/1/4/2;
    color: rgb(79, 79, 79);
    font-family: Montserrat;
    font-size: 10px;
    font-weight: 400;
    line-height: 14px;
  }
  input:focus {
    outline: none;
    border-color: #f7941d;
  }
  .form__button {
    grid-area: 3/2/4/3;
    border: 1px solid rgb(247, 148, 29);
    border-radius: 6px;
    background-color: rgb(247, 148, 29);
    color: white;
    text-align: center;
    font-family: Montserrat;
    font-size: 14px;
    font-weight: 700;
    cursor: pointer;
  }
  
@media all and (max-width: 1575px) {
  .form {
    width: 1180px;
    height: 270px;
    border: 1px solid black;
    border-radius: 20px;
    background: white;
    margin-left: 100px;
    margin-top: 129px;
    position: relative;
    bottom: -40px;
    z-index: 2;
  }
}
@media all and (max-width: 1310px) {
  .form {
    width: 700px;
    height: 500px;
  }
  .form_area {
    position: relative;
    left: 210px;
  }
}
@media all and (max-width: 830px) {
  .form {
    width: 350px;
    height: 600px;
  }
  .form_area {
    display: flex;
    flex-direction: column;
    left: 10px;
    width: 300px;
  }
  :is(.form__name, .form__number, .form__email, .form__button) {
    height: 50px;
  }
  .form__owl {
    top: -10px;
    left: -250px;
  }
  .form__union {
    left: 0px;
  }
  .form__union_text {
    margin-left: 60px;
  }
}
@media all and (max-width: 550px) {
  .form {
    margin-left: 30px;
  }
  .form__owl,
  .form__union {
    display: none;
  }
  .form__union_text {
    width: 300px;
    margin-left: 10px;
  }
}
@media all and (max-width: 500px) {
  .form {
    margin-left: 5px;
  }
}