.teachers {
    width: auto;
    height: 600px;
    background-color: rgba(0, 9, 60, 1);
    position: relative;
  }
  .teachers_line_1 {
    position: absolute;
    top: 410px;
    left: 0px;
    z-index: 1;
  }
  .teachers_line_2 {
    position: absolute;
    top: 118px;
    right: 55px;
    z-index: 1;
  }
  .teachers_circle {
    position: absolute;
    right: 289px;
    bottom: 118px;
    z-index: 1;
  }
  .teachers_cross {
    position: absolute;
    top: 50px;
    left: 292px;
    z-index: 1;
  }
  .teachers_triangle {
    position: absolute;
    bottom: 25px;
    left: 974px;
    z-index: 1;
  }
  .teachers h1 {
    color: white;
    text-align: center;
    font-family: Montserrat;
    font-size: 36px;
    font-weight: 700;
    padding-top: 62px;
  }
  .teachers_cards {
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
    margin-top: 42.5px;
  }
  .teachers_cards_card {
    width: 280px;
    height: 360px;
    border-radius: 20px;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 2;
    gap: 20px;
  }
  .teachers_cards_card:hover {
    transform: scale(1.1);
    transition-duration: 0.7s;
  }
  .teachers_cards_card_img {
    position: relative;
    top: 20px;
  }
  .teachers_cards_card_img_person {
    position: absolute;
    top: 10px;
    left: 10px;
  }
  .teachers_cards_card h5 {
    color: black;
    text-align: center;
    font-family: Montserrat;
    font-size: 18px;
    font-weight: 700;
    margin-top: 25px;
  }
  .teachers_cards_card p {
    width: 240px;
    color: #4f4f4f;
    text-align: center;
    font-family: Montserrat;
    font-size: 14px;
    font-weight: 500;
    line-height: 20px;
  }
  @media all and (max-width: 1200px) {
    .teachers {
      height: auto;
    }
    .teachers_cards {
      padding-bottom: 30px;
    }
    .teachers_cross {
      top: 50px;
      left: 0px;
    }
    .teachers_circle {
      top: 600px;
      right: 50px;
    }
    .teachers_triangle {
      display: none;
    }
  }
  @media all and (max-width: 700px) {
    .teachers_cross {
      top: 300px;
      left: 0px;
    }
    .teachers_line_2 {
      display: none;
    }
  }
  @media all and (max-width: 500px) {
    .teachers_cross,
    .teachers_line_1 {
      display: none;
    }
  }
  